<template>
<v-app id="app">
    <v-main>
        <Navbar v-if="exibirMenuPrincipal()" />

        <router-view style="min-height:100vh"></router-view>
    </v-main>
</v-app>
</template>

<script>
import Navbar from '@/components/Navbar'
import store from '@/store.js'

export default ({
    name: 'App',

    data() {
        return {
            items: [],
            apelido: "",
            navbarMenu: true,
            drawer: true,
            expandOnHover: true,
            cssProps: {
                innerHeight: 500
            }

        }
    },
    components: {
        Navbar
    },
    created() {
        /// se o usurio recarregar a pgina e o mesmo j estiver logado, e como os dados em cache ficam perdidos, o sistema ir redirecionar para a tela d login
        if (store.state.clsUsuario == null || store.state.clsUsuario == undefined || store.state.clsUsuario.length == 0) {
            this.$router.replace('login')
        } else {
            this.$router.replace('home')

        }
    },
    methods: {
        exibirMenuPrincipal() {
            if (store.state.clsUsuario == null || store.state.clsUsuario == undefined || store.state.clsUsuario.length == 0)
                this.navbarMenu = false;
            else
                this.navbarMenu = true;
            return this.navbarMenu;
        },
    },
})
</script>

<style lang="scss">
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    background-color: #000000f1;
}
</style>
