<template>
<nav>

    <v-app-bar height="50px" auto class="black accent-4" dark>
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

        <v-toolbar-title>Family Movies</v-toolbar-title>

        <v-spacer></v-spacer>

        <v-btn icon @click="ClickHome()">
            <v-icon>mdi-home</v-icon>
        </v-btn>

        <v-btn icon @click="Logout()">
            <v-icon>mdi-logout </v-icon>
        </v-btn>

    </v-app-bar>

    <v-navigation-drawer v-model="drawer" width="150px" absolute expand-on-hover permanent class="black accent-4" dark>

        <v-list>        
            <v-divider></v-divider>
            <v-list-item v-for="item in items" :key="item.form" @click="carregarTela(item.form)">
                <v-list-item-icon>
                    <v-icon>{{ item.icon }}</v-icon>
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ item.form_descr }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list>
    </v-navigation-drawer>
</nav>
</template>

<script>
import store from '@/store.js'

export default ({
    data() {
        return {
            drawer: false,
            items: [{
                link: "/Filmes",
                form: "/Filmes",
                form_descr: "Filmes",
                icon:'mdi-movie'
            },
            {
                link: "/Series",
                form: "/Series",
                form_descr: "Series",
                icon:'mdi-television'
            },
            ],

        }
    },
    created() {},

    methods: {

        carregarTela(link) {
            this.$router.push(link).catch(() => {});
        },
        ClickHome() {
            this.$router.push('Home');

        },
        Logout() {
            this.$router.push('login');
            store.commit('resetState');
        }
    },
})
</script>

<style lang="scss">
.textosLabel {
    font-weight: bold;
    font-size: 18px
}

;
</style>
