import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";



Vue.use(Vuex)
  
const getDefaultState = () => {
  return {
      //entity
      clsUsuario:"",
      tipoTelaUsuario: "",
      //urlBackEnd:   'http://sindata.net.br:9088/LeiDoSilencioApi/',
      urlBackEnd:   'http://localhost:58719/',
      cabecalhoValidacao: null,
      clsConfigHeader:null,
      menuNavegacao:null,
      clsPermissoes:null,
      clsListaNavBar:null,
      clsListMenu: null,
      clsLoading:false,
      OcorrenciaAssinatura:null,
  
  }
}
const state = getDefaultState()

export default new Vuex.Store({
  plugins: [createPersistedState(

  )],

  state,
  mutations: {
    setUsuario(state, usuario) {
        state.clsUsuario = usuario
    },
    setCabecalhoValidacao(state, config) {
      state.clsConfigHeader = config
  },
    setTipoTelaUsuario(state, tipoTelaUsuario) {
        state.tipoTelaUsuario = tipoTelaUsuario
    },
    setEsconderMenuNavegacao(state, menuNavegacao){
      state.menuNavegacao = menuNavegacao;
    },
    resetState (state) {
        Object.assign(state, getDefaultState())
    },
    setPemissaoAcesso(state,permissoes){
      state.clsPermissoes = permissoes;
    },
    setListaNavBar(state,listaNavBar){
      state.clsListaNavBar = listaNavBar;
    },
    setOcorrenciaAssinatura(state, OcorrenciaAssinatura){
      state.OcorrenciaAssinatura = OcorrenciaAssinatura;
    },
  },
  actions: {

  },
  
  getters: {

  }
})
