<template>
<div id="login">
    <v-content>
        <v-layout align-start justify-center class="pt-16 pb-16">
            <div class="d-flex align-start mb-12">

            </div>
        </v-layout>
        <v-layout align-center justify-center>
            <v-flex xs12 sm8 md4 class="elevation-12">

                <v-card class="elevation-12">
                    <v-card-text>
                        <v-form ref="formLogin">
                            <v-alert class="red--text text--lighten-1">{{mensagemDialogo}}</v-alert>
                            <v-text-field :rules="validacaoLogin" v-on:keyup.enter="onEnterLogin" v-model="usuario" ref="usuario" value="" label="Usurio" prepend-icon="mdi-account" type="text" />
                            <v-text-field :rules="validacaoLogin" v-on:keyup.enter="onEnterLogin" v-model="senha" ref="senha" value="" label="Senha" prepend-icon="mdi-lock" type="password" />
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="primary" @click="ValidaLogin">Entrar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-flex>
        </v-layout>
            <v-dialog v-model="dialog" width="500">
  
                <v-card>
                    <v-card-title class="text-h5 grey lighten-2">
                        Falha na Autenticao
                    </v-card-title>

                    <v-card-text>
                        Usurio ou Senha Invlida
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="primary" text @click="dialog = false">
                           ok
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

    </v-content>
</div>
</template>

<script>
import store from '@/store.js'

export default {
    components: {

    },
    data() {
        return {
            validacaoLogin: [
                v => !!v || 'Ateno, informe usurio e senha',
            ],
            validacaoSenha: [
                v => !!v || 'Ateno, informe usurio e senha.',
            ],
            usuario: '',
            senha: '',
                    dialog: false,

            mensagemDialogo: '',
            exibirloader: false,
            dialogLoading: false,
            clsUsuario: {
                nome: "",
                senha: null,
            }
        }
    },
    created() {},

    methods: {
        ValidaLogin() {
            if (this.usuario == "familymovies" && this.senha == "sfmaomvliye") {

                this.clsUsuario.nome = this.usuario
                this.clsUsuario.senha = this.senha

                store.commit('setUsuario', this.clsUsuario)
                this.$router.replace('home') 
            } else {
                this.usuario= null
                this.senha = null
                this.dialog = true
            }

        }
    },
}
</script>

